import React, {Component} from 'react';
import {Link} from 'react-router-dom';


class PartsPage extends Component {
    constructor(props) {
        super(props);
        document.title = ' -     ';
    }

    render(){
        return (
            <div>
                <h1></h1>

             
                        <div  >
                                <Link to="/addPart">
                                    <button className="btn-success my-button"> </button>
                                </Link>
                        </div>

                    <br/>

                        <div>
                                <Link to="/partsList">
                                    <button className=" btn-success my-button "> </button>
                                </Link>
                        </div>
                    
                    <br/>

                        <div>
                                <Link to="/">
                                    <button className=" btn-success my-button "> </button>
                                </Link>
                        </div>

            </div>
        );
    }

}

export default PartsPage;