import React, {Component} from 'react';


class ListCarComponent extends Component{


    // constructor(props) {
    //     super(props);

    //     // console.log(props);
    // }

    render() {


        return (
                 
            <tr>
                <td className="made">{this.props.carMade}</td>
                <td className="vin"  style={this.props.browser === 'Android OS' ? {display: 'none'} : {}}>{this.props.vinCode}</td>
                <td className="license">{this.props.licencePlate}</td>  
                <td className="edite" style={this.props.juser.role === 'ROLE_USER' ? {display: 'none'} : {}}> <button onClick={this.props.edit}>I</button> </td>
                <td className="delete" style={this.props.juser.role === 'ROLE_USER' ? {display: 'none'} : {}}> <button onClick={this.props.delete}></button> </td>
                <td className="add" style={
                    this.props.juser.role === 'ROLE_USER' ? 
                    (this.props.cust === undefined ?  {display: 'none'} : {}) 
                    : {display: 'none'}
                    }>  <button   onClick={this.props.add}></button> </td>
                <td className="create" style={
                    this.props.juser.role === 'ROLE_USER' ? 
                    (this.props.cust === undefined ?  {} : {display: 'none'}) 
                    : {display: 'none'}
                    }>  <button   onClick={this.props.create}> </button> </td>
                <td className="allOrders" style={
                    this.props.cust === undefined ?  {} : {display: 'none'} 
                    }>  <button   onClick={this.props.allOrders}>i </button> </td>
            </tr> 

        )
    }


}

export default ListCarComponent;