import React, {Component} from 'react';
import {Link} from 'react-router-dom';

class UsersPage extends Component {

    constructor(props) {
        super(props);
        document.title = 'i -     ';
    }

    render(){
        return (
            <div>
                <h1>i</h1>

                        <div  >
                                <Link to="/addUser">
                                    <button className="btn-success my-button"> </button>
                                </Link>
                        </div>

                    <br/>

                        <div>
                                <Link to="/userList">
                                    <button className="btn-success my-button"> </button>
                                </Link>
                        </div>

                    


            </div>
        );
    }




}

export default UsersPage;