import React, {Component} from 'react';
import {Link} from 'react-router-dom';


class CustomersPage extends Component {
    constructor(props) {
        super(props);
        document.title = 'i -     ';
    }

    render(){
        return (
            <div>
                <h1>i</h1>

                        <div  >
                                <Link to="/newCustomer">
                                    <button className="btn-success my-button"> i</button>
                                </Link>
                        </div>

                    <br/>

                        <div>
                                <Link to="/customerList">
                                    <button className=" btn-success my-button"> i</button>
                                </Link>
                        </div>

                    <br/>

                        <div>
                                <Link to="/">
                                    <button className=" btn-success my-button "> i</button>
                                </Link>
                        </div>


            </div>
        );
    }

}

export default CustomersPage;