import React, {Component} from 'react';


class ListUserComponent extends Component{



    render() {


        return (
                 
            <tr>
                <td className="">{this.props.name}</td>
                <td className="">{this.props.role === 'ROLE_ADMIN' ? "i" : ""}</td>
                <td className="" style={this.props.workId === undefined ?  {} : {display: 'none'}}>  <button   onClick={this.props.edit}>Ii</button> </td>
                <td className="" style={this.props.workId === undefined ?  {} : {display: 'none'}}>  <button   onClick={this.props.delete}></button> </td>
                <td className="" style={this.props.workId === undefined ?  {display: 'none'} : {}}>  <button   onClick={this.props.select}></button> </td>
                <td className="" style={this.props.workId === undefined ?  {} : {display: 'none'}}>  <button   onClick={this.props.report}></button> </td>
            </tr> 

        )
    }


}

export default ListUserComponent;