import React, {Component} from 'react';
import {Link} from 'react-router-dom';

class WorksPage extends Component {
    constructor(props) {
        super(props);
        document.title = ' -     ';
    }

    render(){
        return (
            <div>
                <h1></h1>

                        <div  >
                                <Link to="/addWork">
                                    <button className="btn-success my-button"> </button>
                                </Link>
                        </div>

                    <br/>

                        <div>
                                <Link to="/worksList">
                                    <button className=" btn-success my-button"> </button>
                                </Link>
                        </div>

                    <br/>

                        <div>
                                <Link to="/">
                                    <button className=" btn-success my-button "> </button>
                                </Link>
                        </div>


            </div>
        );
    }



}

export default WorksPage;