import React, {Component} from 'react';
import './ListCustomerComponent.css'

class ListCustomerComponent extends Component{

    // constructor(props) {
    //     super(props);

    //     // console.log(props);
    // }

    render() {


        return (
                 
            <tr>
                <td className="name">{this.props.name}</td>
                <td className="telNumber">{this.props.telNumber}</td>
                <td className="edite" style={this.props.juser.role === 'ROLE_USER' ? {display: 'none'} : {}}> <button onClick={this.props.edit}>Ii</button> </td>
                <td className="delete" style={this.props.juser.role === 'ROLE_USER' ? {display: 'none'} : {}}> <button onClick={this.props.delete}></button> </td>
                <td className="add" style={
                    this.props.juser.role === 'ROLE_USER' ? 
                    (this.props.car === undefined ?  {display: 'none'} : {}) 
                    : {display: 'none'}
                    }>  <button   onClick={this.props.add}></button> </td>
                <td className="create" style={
                    this.props.juser.role === 'ROLE_USER' ? 
                    (this.props.car === undefined ?  {} : {display: 'none'}) 
                    : {display: 'none'}
                    }>  <button   onClick={this.props.create}> </button> </td>
                <td className="allOrders" style={
                    this.props.car === undefined ?  {} : {display: 'none'} 
                    }>  <button   onClick={this.props.allOrders}>i </button> </td>
            </tr> 

        )
    }


}

export default ListCustomerComponent;